.navlist{
  width:20%;
  display:block;
  margin:0 auto;
  margin-top:2%;
  text-align: center;
  border:2px solid#eee;
  padding:5%;
}
.navlist a{
  margin:2%;
  font-size:1.2em;
}
.navlist div{
  font-size:1.2em;
  padding-top:2%;
  color:#088;
  font-weight: bold;
}
.imglist{
  width:20%;
  display:block;
  margin:0 auto;
  margin-top:2%;
  text-align: center;
  border:2px solid#eee;
  padding:5%;
}
.imglist img{
  width:100%;
}

.timelist{
  width:20%;
  display:block;
  margin:0 auto;
  margin-top:2%;
  text-align: center;
  border:2px solid#eee;
  padding:5%;
}
.timelist span{
  width:100%;
  font-size:1.5em;
  color:#088;
  font-weight: bold;
  display:block;
}

.mainnav{
  width:40%;
  display:block;
  margin:0 auto;
  margin-top:2%;
  text-align: center;
}
.mainnav nav{
  width:100%; 
  display: block;
  margin:0 auto;
}
.mainnav nav a{
  padding:4%;
  font-size:1.4em;
}